//        - related-products,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.related-products {
  $block-name: &; // #{$block-name}__element {}

  &_item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 25px;
    padding-top: 15px;
    border-radius: 3px;
    transition: all 0.2s ease-in-out;

    @media (max-width: 1199px) {
      padding: 20px;
    }

    &:hover,
    &:focus {
      text-decoration: none;
      box-shadow: 0 0 15px rgba(81, 92, 98, 0.3);
    }
  }

  &_preview {
    width: 75%;
    height: auto;
    transition: transform 0.5s ease;

    &:hover {
      transform: scale(1.07);
      transition: transform 2.5s ease;
    }
  }

  &_title {
    margin-top: auto;
    font-weight: 400;
    font-size: 17px;
    line-height: 1.35;
    text-align: center;
    color: rgba($text-color, 0.8);

    @media (max-width: 1199px) {
      margin-top: 10px;
      font-size: 15px;
      line-height: 1.3;
    }

    @media (max-width: 575px) {
      font-size: 12px;
    }
  }

  &_coast {
    font-weight: 300;
    font-size: 15px;
    color: rgba($text-color, 0.6);

    @media (max-width: 575px) {
      font-size: 12px;
    }
  }
}
