.phone {
  $block-name: &; // #{$block-name}__element {}

  &_wrapper {
    display: flex;
    flex-direction: column;
  }

  &_number {
    font-weight: 900;
    font-size: 64px;
    line-height: 0.6;
    color: #e42021;
    transition: color 0.3s ease-in-out;

    &:hover,
    &:focus {
      color: $color-main--hover;
      text-decoration: none;
      outline: none;
    }

    &:active {
      color: $color-main--active;
    }
  }
}
